import React, { Component } from 'react';

export default class Adsense extends Component {
  componentDidMount () {
    const script = document.createElement("script");
    script.text = "(adsbygoogle = window.adsbygoogle || []).push({});"
    document.body.appendChild(script);
  }

  render() {
    return (
      <ins
        class="adsbygoogle"
        style={this.props.style}
        data-ad-client="ca-pub-3334034318594694"
        data-ad-slot={this.props.dataAdSlot}
        data-ad-format={this.props.dataAdFormat}
        data-full-width-responsive={this.props.dataFullWidthResponsive}
      >
      </ins>
    );
  }
};