import React from 'react';

import HashTag from '../HashTag/HashTag.js';

const HashTagSet = (props) => {
    const hashTags = props.children.split("#").filter(Boolean);
    const hashTagSet = hashTags.map((hashTag) => 
        <HashTag {...props}>
            #{hashTag}
        </HashTag>
    );
    return hashTagSet;
}

export default HashTagSet;