import React from 'react';

import './HashTag.css';

const HashTag = (props) => {
    var { className, ...rest } = props;
    if (className == undefined) {
        className = "HashTag";
    }
    else {
        className += " HashTag";
    }
    return (
        <div className={className} {...rest}>
            {props.children}
        </div>
    );
}

export default HashTag;